import React, { Component } from 'react';
import PropTypes from 'prop-types'
import Navbar from '../user/Navbar';
import './Admin.css'

import $ from 'jquery'

class Admin extends Component {

    constructor(props) {
        super(props);
        this.state = {
            account: '',
            title: '',
        }
    }

    handleAccountChange (event) {
        this.setState({
            account: event.target.value
        })
    }

    handleTitleChange (event) {
        this.setState({
            title: event.target.value
        })
    }

    handleQueryUser () {
        this.serverRequest = $.post("/MybkServlet/queryUser",{username:this.state.account},function(data){
            console.log(data),
            alert(data)
        }.bind(this)); 
    }

    handleQueryBook () {
        this.serverRequest = $.post("/MybkServlet/queryBook",{title:this.state.title},function(data){
            console.log(data),
            alert(data)
        }.bind(this)); 
    }

    render() {
        return (
            <div>
                <Navbar/>
            <div className="wrapper">
                <div className='admin-field'>
                    <span className='admin-field-name'>用户名：</span>
                    <div className='admin-field-input'>
                        <input
                            value={this.state.account}
                            onChange={this.handleAccountChange.bind(this)} />
                    </div>
                </div>
                <div className='admin-field-button'>
                    <button
                        onClick={this.handleQueryUser.bind(this)}>
                        查询
                    </button>
                </div>
                <div className='admin-field'>
                    <span className='admin-field-name'>书名：</span>
                    <div className='admin-field-input'>
                        <input
                            value={this.state.title}
                            onChange={this.handleTitleChange.bind(this)} />
                    </div>
                </div>
                <div className='admin-field-button'>
                    <button
                        onClick={this.handleQueryBook.bind(this)}>
                        查询
                    </button>
                </div>
            </div>
            </div>
        );
    }
}

export default Admin;


